import {Component, OnInit} from '@angular/core';
import {trigger, state, style, transition, animate} from '@angular/animations';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  selectedNav;

  constructor() {
  }

  ngOnInit() {

  }


  changeSection($event) {
    this.selectedNav = $event.active;
    let activeComponent = document.querySelector('#' + this.selectedNav);

    if ($event.inactive != $event.active) {
      let inactiveComponent = document.querySelector('#' + $event.inactive);
      inactiveComponent.classList.add('animateOut');


      setTimeout(function () {
        inactiveComponent.classList.remove('animateOut');
        inactiveComponent.classList.remove('isShowing');
        activeComponent.classList.add('animateIn');
        activeComponent.classList.add('isShowing');
      }, 250);

      setTimeout(function () {
        activeComponent.classList.remove('animateIn');
      }, 550);
    }
    else {

    }

  }
}
